body{
    text-align: center;

}
.addArticle{
    text-align: center;
    padding-top: 50px;
    width: 350px;
    height: 400px;
    border-radius: 10px;
    display: inline-block;
    justify-content: center;
    position: relative; 
    font-family: 'Source Serif 4', sans-serif;
    
}
.addForm{
    width: 100%;
    height: 100%;
    position: relative;
       
}
label{
	font-size: 20px;
	justify-content: center;
	display: flex;
	cursor: pointer;
	transition: .5s ease-in-out;
}
input{
    font-size: 20px;
    padding: 10px;
	height: 20px;
	background: #e0dede;
	justify-content: center;
	display: flex;
	margin: 10px auto;
	border: none;
	outline: none;
	border-radius: 5px;
}
button{
	width: 60%;
	height: 40px;
	margin: 10px auto;
	justify-content: center;
	display: block;
	color: white;
    background-color: teal;
	font-size: 1em;
	margin-top: 10px;
	outline: none;
	border: none;
	border-radius: 5px;
	transition: .2s ease-in;
	cursor: pointer;
} 

