.article{
    flex: 9;
    font-family: 'Source Serif 4', sans-serif;
}
.articleWrapper{
    padding: 30px;
    padding-right: 0;
}

.articleImg{
    width: 75%;
    height: 300px;
    border-radius:5px ;
    object-fit: cover;
}
.articleTitle{
    text-align: center;
    margin: 10px;
    font-size: 20px;
}
.articleEdit{
    float: right;
    font-size: 20px;
}
.articleIcon{
    margin-left: 10px;
    cursor: pointer;
    float: right;
    font-size: 30px;
}

.Edit{
    color: teal;
}
.Delete{
    color: tomato;
}
.articleDate{
    margin-bottom: 20px;
    display: flex;
    font-size: 16px;
    color: #b395;
}
p{
   color: #666;
    font-size: 18px;
    line-height: 25px;
    text-align: justify;
    

}
p::first-letter{
    margin-left: 20px;
    font-size: 25px;
    font-weight: 500;
}
.articleInfo{
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    font-family: 'Source Serif 4', sans-serif;
}
.articleButton{
    width: 100px;
    border: none;
    background-color: tomato;
    padding: 5px;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    align-self: flex-end;
}
