.singleArticle{
    width: 300px;
    margin: 5px 40px 40px 40px;
    font-family: 'Source Serif 4', sans-serif;
}
.singleArticleImg{
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 5px;
}


.singleArticleTitle{
    
    font-size: 24px;
    font-weight: 700;
    margin-top:15px ;
    
}

.singleArticleDate{
    
    font-style: italic;
    font-size: 13px;
    color: #999;
}
.singleArticleInfo{
    
    font-size: 12px;
    color: #444;
    margin-top: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
}