.nav {
  width: 100%;
  height: 50px;
  background-color: aquamarine;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  font-family: "Source Serif 4", sans-serif;
}

.navLeft{
    flex: 3;
    margin-left: 1em;
    align-items: left;
    display: flex;
    justify-content: left;
    color: white;
    font-family: 'Source Serif 4', sans-serif;
}

.navCenter{
    flex: 5;
}

.navRight{
    flex: 3;
    margin-right: .5em;
    align-items: right;
}

.navList{
    display: flex;
    justify-content: center;
    margin:0 ;
    padding: 0;
    list-style: none;
}

.navListItem{
    margin-right: 20px;
    font-size:18px ;
    cursor: pointer;
}


