body{
    background: url("https://image.freepik.com/free-vector/hand-painted…ercolor-nature-background_23-2148941599.jpg?w=740");
    background-size: cover;
    font-family: 'Source Serif 4', sans-serif;
    
    
}
.signup{
    font-family: 'Source Serif 4', sans-serif;
    height: calc(100vh-50px); 
    display: flex; 
    flex-direction: column;
    justify-content: center;
}
.signupTitle{
    font-size: 50px;
    color: darkgreen;
    margin-top: 20px;
}
.signupForm{
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    /* border: darkgreen solid; */
    width: 500px;
    height: 400px;
    align-content: center;
    margin-left: 30%;
    margin-right: 30%;
    
}
label{
    margin: 10px 0;
}

input{
    padding: 10px;
    background-color: white;
    border: none;
    font-family: 'Source Serif 4', sans-serif;
}

.signupBtn{
    margin-top: 20px;
    cursor: pointer;
    background-color:teal;
    border: none;
    color: white;
    border-radius: 10px;
    padding: 10px;
    width: 80px;
}

.loginBtn{
    position: absolute;
    top: 60px;
    right: 20px;
    background-color: lightcoral;
    cursor: pointer;
    border: none;
    padding: 10px;
    color: white;
    width: 80px;
}
.error{
    color: red;
}